@import 'global.scss';

:host {
  /**
   * @prop --card-border-width: The border width of the card.
   * @prop --card-border-style: The style of the border of the card.
   * @prop --card-border-color: The color of the border of the card.
   * @prop --card-hover-border-color: The color of the border of the card on hover.
   * @prop --card-border-radius: The border radius of the card.
   * @prop --card-border-transition: The transition of the border of the card.
   * @prop --card-box-shadow: The shadow of the card.
   * @prop --card-box-shadow-animated-hover: The animation of the box shadow of the card on hover.
   * @prop --card-container-transition: The transition of the container of the card.
   * @prop --card-inner-padding: The inner padding of the card.
   */
  --card-border-width: 2px;
  --card-border-style: solid;
  --card-border-color: #{$rwc-color-border-primary};
  --card-hover-border-color: #{$rwc-color-border-active};
  --card-border-radius: #{$rwc-spacing--sm};
  --card-border-transition: border-bottom-color 0.4s, border-left-color 0.4s, border-right-color 0.4s, border-top-color 0.4s, width 0.4s ease-in-out, height 0.4s ease-in-out;
  --card-box-shadow: #{$rwc-box-shadow-lg};
  --card-box-shadow-animated-hover: #{$rwc-box-shadow-xl};
  --card-container-transition: box-shadow 0.4s ease-in-out;
  --card-inner-padding: #{$rwc-spacing--md};
}

.container {
  border-radius: var(--card-border-radius);
  padding: var(--card-inner-padding);
  position: relative;
  transition: var(--card-container-transition);
}

.shadow {
  box-shadow: var(--card-box-shadow);
  
  &:hover {
    box-shadow: var(--card-box-shadow-animated-hover);
  }
}

.border:not(.animate-border) {
  border-color: var(--card-border-color);
  border-style: var(--card-border-style);
  border-width: var(--card-border-width);
  border-radius: var(--card-border-radius);
  transition: border-color 100ms linear;

  &:hover {
    border-color: var(--card-hover-border-color);
  }
}

.animate-border {
  &::before,
  &::after {
    border-color: transparent;
    border-style: var(--card-border-style);
    border-width: var(--card-border-width);
    border-radius: var(--card-border-radius);
    content: "";
    height: 0;
    width: 0;
    position: absolute;
    transition: var(--card-border-transition);
  }

  &::before {
    left: 0;
    top: 0;
  }

  &::after {
    right: 0;
    bottom: 0;
  }

  &:hover {
    &::before,
    &::after {
      height: calc(100% - var(--card-border-width) * 2);
      width: calc(100% - var(--card-border-width) * 2);
    }

    &::before {
      border-top-color: var(--card-hover-border-color);
      border-left-color: var(--card-hover-border-color);
    }

    &::after {
      border-bottom-color: var(--card-hover-border-color);
      border-right-color: var(--card-hover-border-color);
    }
  }
}

.pointer:hover {
  cursor: pointer;
}
